@import url(https://fonts.google.com/specimen/Nunito?sidebar.open&selection.family=Archivo);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

body {
    font-family: 'Archivo', sans-serif;
    }
.join-container{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

}
.join-form{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 30px;
    /* border: 1px solid skyblue; */
    border-radius: 30px;
    box-shadow:  20px 20px 60px #d9d9d9, 
                -20px -20px 60px #ffffff;
    transition: .5s;
}


.join-form:hover{
    /* border: 2px solid skyblue; */
    transform: scale(1.02);
    transition: .5s;
}

.join-form h2{
    text-align: center;
    margin: 3px;
}

.join-form label{
    font-weight: bold;
    font-size: 14px;
}

.join-form input{
    border-radius: 5px;
    border: 2px solid skyblue;
    transition: .5s;
    padding: 2px;
}

.join-form input[type=text]:focus{
    outline: none;
    border: 2px solid skyblue;
    transition: .5s;
    transform: scale(1.05);
}



.join-form input[type=submit]{

    color: #ffffff;
    border: none;
    width: 50%;
    margin :0 auto;
    margin-top:  10px;
    padding: 5px;
    font-weight: bold;
    border-radius: 5px;
    background: #2C4CD8;
    box-shadow:  45px 45px 90px #bababa, 
    -45px -45px 90px #ffffff;
    cursor: pointer;
} 

.join-form input[type=submit]:focus{
    outline: none;
}
.chat-container{
    display: flex;
    flex-direction: column;
    align-items: stretch;
    height: 100vh;
}
.infobar-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgb(109, 163, 209);
    padding: 10px;
    background-color: rgb(0, 146, 220);
}
.room-name{
    display: flex;
    justify-content: center;
    flex-grow: 1;
    font-size: 16px;
    color: #ffffff;
    font-weight: bold;
}
.close{
    color: rgb(255, 255, 255);
    font-size: 20px;
}
.messagesend-container{
    display: flex;
    flex-direction: row;
    height: 50px;
    border-top: 1px solid rgb(213, 213, 213);
    border-bottom: 3px solid rgb(0, 146, 220);
}



.messagesend-container input{
    border: none;
    flex-grow: 1;
    padding: 5px;
    padding-left: 10px;
    
}

.messagesend-container input:focus{
    outline: none;
}

.messagesend-container span{
    color: rgb(28, 110, 211);
    font-size: 30px;
    display: flex;
    align-items: center;
}
.messages-section-container{
    display: flex;
    flex-grow: 1;
    flex-direction: column;
}

.message-box-wrapper{
    display: flex;
    width: 100%;
}

.message-box{
    width: auto;
    display: flex;
    flex-direction: column;
    max-width: 50%;
    min-width: 20%;
    border: 1px solid rgb(163, 209, 255);
    border-radius: 5px;
    
    margin: 5px;
}

.message-box-wrapper .user{
    font-weight: bold;
    font-size: 16px;
    background-color: rgb(44, 134, 243);
    color: #ffffff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding-left: 5px;
 
    /* padding: 5px; */
}

.message-box-wrapper .message{
    padding: 5px;
    border-radius: 5px;
}
.left{
    justify-content: flex-start;
}
.right{
    justify-content: flex-end;
    
}

.right .message-box{
    background-color:  rgb(44, 134, 243);
    color: #ffffff;
}
